* {
  box-sizing: border-box;
}

.back-button {
  color: #0ab7fb;
  text-align: center;
  cursor: pointer;
  background-color: #b6b4b480;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 2em;
  font-weight: 999;
  line-height: 0;
  position: absolute;
  top: -5px;
  left: 10px;
}

@media (width <= 768px) {
  .back-button {
    width: 30px;
    height: 30px;
    font-size: 1.5em;
    top: 1em;
  }
}

.loader {
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: 2s linear infinite spin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (width <= 768px) {
  .loader {
    width: 50px;
    height: 50px;
  }
}
